<template>
  <div class="hello">
    <div class="topMenuBox">
      <div class="topMenuBox-left">
        <img src="../assets/log.png" alt style="width:140px;height:35px;margin-top:0.8vh" />
      </div>
      <div class="topMenuBox-right">
        <div
          class="topMenuBox-right-cell"
          v-for="(item,index) in menuBox"
          :key="index"
          @click="mainMenuClick(index)"
          :class="item.flag?'menuColor':''"
          @mouseenter="mouseenter(index)"
        >
          <template>
            <div class="firstMenuSelectBox" v-if="item.flag2" @mouseleave="mouseleave(index)">
              <div
                class="firstMenuSelectBoxCell"
                v-for="(itemSelect,indexSelect) in menuBox2"
                @click="itemSelectClick(indexSelect)"
                :class="itemSelect.flag?'firstMenuSelectBoxCell2':''"
              >{{itemSelect.name}}</div>
            </div>
          </template>
          <div class="topMenuBox-right-cell-b" v-if="item.flag"></div>
          {{item.name}}
        </div>
      </div>
      <div class="topMenuBox-l">
        <div class="topMenuBox-l-imgDiv" style="cursor: pointer;" @click="outFun">
          <img src="../assets/close.png" alt />
        </div>
        <div class="topMenuBox-l-imgDiv">
          <img src="../assets/log2_03.png" alt />
        </div>
        <div
          style="color:white"
          class="cursorPointer"
          @click="userNameFun"
        >{{$store.state.username}}</div>
        <div
          style="color:white;margin-left:10px"
          class="cursorPointer"
          @click="downBz"
        >帮助</div>
      </div>
    </div>
    <router-view name="mainView"></router-view>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false"   v-model="modal1" width="800" title class="dialog">
      <div style="height:400px;overflow-y: auto;">
        <div class="userNameBox">
          <div class="userNameBox-top flexStart">
            <div class="userNameBox-top-1" style="width:100%">
              <!-- <div class="userNameBox-top-1-title" style="height:90px">基本信息</div> -->
              <div class="userNameBox-top-1-body">
                <div class="userNameBox-top-1-bodyCell flexStart">
                  <div class="serNameBox-top-1-bodyCellLeft">登录账号：</div>
                  <div class="serNameBox-top-1-bodyCellRight">{{formValidate.loginid}}</div>
                </div>
                <div class="userNameBox-top-1-bodyCell flexStart">
                  <div class="serNameBox-top-1-bodyCellLeft">所属部门：</div>
                  <div class="serNameBox-top-1-bodyCellRight">{{formValidate.dpname}}</div>
                </div>
                <div class="flexStart">
                  <div class="serNameBox-top-1-bodyCellLeft">角色：</div>
                  <div class="serNameBox-top-1-bodyCellRight flexStartWrap" style="height:auto">
                    <span
                      v-for="(item,index) in formValidate.roleNames"
                      style="margin-right:10px;display:inline-block;height:30px;"
                    >{{item}}</span>
                  </div>
                </div>
                <div class="userNameBox-top-1-bodyCell flexStart">
                  <div class="serNameBox-top-1-bodyCellLeft">真实姓名：</div>
                  <div class="serNameBox-top-1-bodyCellRight">{{formValidate.username}}</div>
                </div>
                <Form
                  ref="formValidate"
                  :model="formValidate"
                  :label-width="110"
                  :rules="ruleValidate"
                  inline
                >
                  <FormItem label="电话：" style="width:100%" prop="phone">
                    <Input v-model="formValidate.phone" placeholder></Input>
                  </FormItem>
                  <FormItem label="手机：" prop="mobile" style="width:100%">
                    <Input v-model="formValidate.mobile" placeholder></Input>
                  </FormItem>
                  <FormItem label="邮箱：" prop="mailbox" style="width:100%">
                    <Input v-model="formValidate.mailbox" placeholder></Input>
                  </FormItem>
                  <FormItem label="职务：" style="width:100%" prop="post">
                    <Input v-model="formValidate.post" placeholder></Input>
                  </FormItem>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="cancelFun">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ===========弹框 -->
  </div>
</template>

<script>
export default {
  created() {},
  mounted() {},
  name: "HelloWorld",
  data() {
    return {
      // =====================弹框一
      modal1: false,
      formValidate: {
        loginid: "",
        dpname: "",
        userid: "",
        username: "",
        phone: "",
        mobile: "",
        mailbox: "",
        post: ""
      },
      ruleValidate: {
        phone: [
          { type: 'string', max: 11, message: '长度最大不能超过11字', trigger: 'blur' }
        ],
        mobile: [
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "手机格式不正确",
            trigger: "blur"
          }
        ],
        mailbox: [
          { type: "email", message: "邮箱格式不正确", trigger: "blur" },
          { type: 'string', max: 32, message: '长度最大不能超过32字', trigger: 'blur' }
        ],
        post: [
           {
            type: "string",
            max: 32,
            message: "长度最大不能超过32",
            trigger: "blur"
            }
        ]
      },
      // =====================弹框一
      menuBox2: [
        //一级下拉
        { name: "统一客户评级", flag: false },
        { name: "零售客户评级", flag: false },
        { name: "投行客户评级", flag: false },
        { name: "资管客户评级", flag: false }
      ],
      menuBox: this.$store.state.menuBox
    };
  },
  watch: {
    "$store.state.menuBox": {
      handler(newVal) {
        this.menuBox = newVal;
        this.storage.set("menuBox", newVal);
      },
      deep: true
    },
    "$store.state.menuBoxIndex": {
      handler(newVal) {
        this.storage.set("menuBoxIndex", newVal);
      }
    }
  },
  methods: {
    getCokies(cookieName) {
      var strCookie = document.cookie;
      var arrCookie = strCookie.split("; ");
      for (var i = 0; i < arrCookie.length; i++) {
        var arr = arrCookie[i].split("=");
        if (cookieName == arr[0]) {
          return arr[1];
        }
      }
      return "";
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          let data = {
            loginid: this.formValidate.loginid,
            phone: this.formValidate.phone,
            mobile: this.formValidate.mobile,
            mailbox: this.formValidate.mailbox,
            post: this.formValidate.post
          };
          data = JSON.stringify(data);
          this.axios
            .post("/system/user/updateUser", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                setTimeout(() => {
                  this.modal1 = false;
                }, 2000);
              }
            })
            .catch(error => {});
        }
      });
    },
    downBz() {
      this.axios
      .get(`/system/localDownloads`,{
        headers: { "Content-Type": "application/json" },
        responseType: "blob"
      })
      .then(res => {
        this.download(res, "帮助.pdf");
      })
      .catch(function(error) {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    userNameFun() {
      this.modal1 = true;
      this.axios
        .post("/system/user/selectUserInfo")
        .then(response => {
          if (response.code == "200") {
            this.formValidate.loginid = response.data.loginid;
            this.formValidate.dpname = response.data.dpname;
            this.formValidate.username = response.data.username;
            this.formValidate.roleNames = [];
            response.data.roles.forEach(e => {
              this.formValidate.roleNames.push(e.roleName);
            });
            this.formValidate.phone = response.data.phone;
            this.formValidate.mobile = response.data.mobile;
            this.formValidate.mailbox = response.data.mailbox;
            this.formValidate.post = response.data.post;
          }
        })
        .catch(error => {});
    },
    cancelFun() {
      this.modal1 = false;
    },
    outFun() {
      let that = this;
      this.axios
        .post("/system/logout", {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            // -----------
            this.$store.commit("menuBoxIndex", {
              menuBoxIndex: 1
            });
            this.$store.commit("menuBox", {
              menuBox: []
            });
            this.storage.set("leftMenuBox", "");
            this.$store.commit("leftMenuBox", {
              leftMenuBox: ""
            });
            this.storage.set("leftMenuBoxBody", []);
            this.$store.commit("leftMenuBoxBody", {
              leftMenuBoxBody: []
            });
            this.storage.set("username", "");
            this.$store.commit("username", {
              username: ""
            });
            this.storage.set("firstLoginFlag", false);
            this.$store.commit("firstLoginFlag", {
              firstLoginFlag: false
            });
            // -----------
            this.deleteCookies("AmlLtpaToken");
            this.deleteCookies("LtpaToken2");
           
            this.$router.push({ name: "signInTo" });
          }
        })
        .catch(error => {});
    },
    sigiIntest() {
      this.$router.push({ name: "signInTo" });
    },
    itemSelectClick(index) {
      this.menuBox2.forEach(element => {
        element.flag = false;
      });
      this.menuBox2[index].flag = true;
      this.$router.push({ name: "other" });
    },
    deleteCookies(name) {
      this.setCookie(name,"",0);
    },
    setCookie(name, value, day) {
      var date = new Date();  
      date.setTime(date.getTime() + 24 * 60 * 60 * 1000 * day); 
//      window.document.cookie = name + "=" + value + ";path=/;expires=" + date.toGMTString();//开发环境
        window.document.cookie = name + "=" + value + ";path=/;domain=.gf.com.cn;expires=" + date.toGMTString();//线上环境
   },
    mouseenter(index) {
      if (this.menuBox[index].name == "客户评级") {
        this.menuBox[index].flag2 = true;
        //无需commit store
        this.menuBox.forEach((element, index) => {
          element.flag = false;
        });
      } else {
        this.menuBox.forEach(e => {
          e.flag2 = false;
          if (e.sysNo == this.$store.state.menuBoxIndex) e.flag = true;
          else e.flag = false;
        });
        this.menuBox2.forEach(element => {
          element.flag = false;
        });
      }
    },
    mouseleave(index) {
      this.menuBox[index].flag2 = false;
      this.menuBox.forEach(e => {
        if (e.sysNo == this.$store.state.menuBoxIndex) e.flag = true;
        else e.flag = false;
      });
      this.menuBox2.forEach(element => {
        element.flag = false;
      });
    },
    mainMenuClick(index) {
      if (this.menuBox[index].name == "客户评级") {
        return false;
      } else {
        let menuName = this.menuBox[index].name;
        let url = this.menuBox[index].url;
        let menuCode = this.menuBox[index].sonMenuCode
        if (this.menuBox[index].url) {
          url = this.menuBox[index].url;
        } else {
          url = "/other";
        }
        switch (menuName) {
          case "工作中心":
            // this.$router.push({ name: "workCenter" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({ name: "workCenter" });
            }
            break;
          case "客户管理":
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          case "检查数据提取":
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          case "审核任务":
            // this.$router.push({ path: "/dataAuditExtrackApply/1" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          case "洗钱风险评估":
            // this.$router.push({ name: "launderBussiness" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          case "系统管理":
            // this.$router.push({ name: "userManagement" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          case "过程管理":
            // this.$router.push({ name: "processClass" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            //  this.$router.push({ name: "processClass" });
            break;
          case "统计分析":
            // this.$router.push({ name: "processClass" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            //  this.$router.push({ name: "processClass" });
            break;
          case "模型管理":
            // this.$router.push({ path: "/riskLevelSet/1" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$router.push({path: url, query: { menuCode: menuCode}});
            }
            break;
          default:
            // this.$router.push({ name: "other" });
            if (this.$store.state.menuBoxIndex != this.menuBox[index].sysNo) {
              this.$store.commit("menuBoxIndex", {
                menuBoxIndex: 88
              });
              this.$router.push({ name: "other" });
            }
        }
      }
    }
  }
};
</script>

<style scoped>
.menuColor {
  color: RGB(238, 253, 5);
}
/* .mainViewCell-leftBox .el-menu{
  background-color: pink!important;
} */
.firstMenuSelectBox {
  width: 100px;
  min-height: 100px;
  background-color: RGB(29, 102, 206);
  position: absolute;
  left: -10px;
  bottom: -122px;
  border-radius: 6px;
  z-index: 1000;
  font-size: 14px;
}
.firstMenuSelectBoxCell {
  width: 100%;
  height: 30px;
  line-height: 30px;
  padding: 3px;
}
.firstMenuSelectBoxCell2 {
  color: RGB(238, 253, 5);
}
</style>



